








































import Vue from 'vue';
import ArrowLine from '@/vue/ArrowLine.vue';

export default Vue.extend({
  components: {
    'arrow-line': ArrowLine,
  },
  props: {
    x: {
      type: Number,
      default: 1,
    },
    y: {
      type: Number,
      default: 1,
    },
    width: {
      type: Number,
      default: 1,
    },
    height: {
      type: Number,
      default: 1,
    },
    strokeColor: {
      type: String,
      default: 'rgb(163, 193, 226)',
    },
    strokeWidth: {
      type: Number,
      default: 2,
    },
    fillColor: {
      type: String,
      default: 'rgb(221, 231, 242)',
    },
    hatchColor: {
      type: String,
      default: '#AAA',
    },
    sawtooth: {
      type: Boolean,
      default: true,
    },
    sawToothSize: {
      type: Number,
      default: 10,
    },
    showArrow: {
      type: Boolean,
      default: true,
    },
    showImage: {
      type: Boolean,
      default: true,
    }
  },
  data(): Record<string, unknown> {
    return {
      id: null,
    }
  },
  methods: {
    pointsForImagePoly(): string {
      const x = this.x + 1;
      let x2 = x + this.width;
      const y = this.y;
      let y2 = this.y + this.height;
      const lastSawToothAdjust = this.width % (this.sawToothSize * 2);
      let polyPoints = `${x},${y2 - lastSawToothAdjust} ${x},${y} ${x2},${y} ${x2},${y2}`;
      if (this.sawtooth) {
        let yStep: number = this.sawToothSize;
        let xStep: number = this.sawToothSize;
        // Loop through to add the "saw" look
        while (x2 > x) {
          polyPoints += ` ${x2},${y2}`;
          x2 -= 20;
          y2 -= yStep;
          yStep *= -1;
        }
        polyPoints += ` ${x},${y2 + x2 - x}`;
        polyPoints += ` ${x},${this.y + this.height - (this.sawToothSize * 2)}`;
      } else {
        polyPoints += ` ${x},${y2}`;
        polyPoints += ` ${x},${y2 - (this.sawToothSize * 2)}`;
      }

      return polyPoints;
    },
    placeholderX(): number {
      return this.x + ((this.width / 2) - (this.placeholderWidth() / 2));
    },
    placeholderWidth(): number {
      let calc: number = this.width / 5;
      return Math.max(calc, 50);
    },
  }
});
